	import uniLoadMore from "../../components/uni-load-more/uni-load-more.vue"
	export default{
	    components: {uniLoadMore},
		data() {
			return {
                ismore:false,
                contentText: {
                    contentdown: '查看更多',
                    contentrefresh: '加载中',
                    contentnomore: '没有更多',
                },
				page:1,
                size:10,
                
				shareList:[]
			};
		},
		onLoad() {
			this.getData()
		},
        onPullDownRefresh() {
            
            this.page = 1
            this.getData()
            
        },
        onReachBottom() {
       
            this.status = 'loading'
            uni.showNavigationBarLoading()
            
            this.page++
            this.getData()
        },
		computed: {
			communityId(){
				return this.$store.getters.communityId
			}
		},
		methods: {
			getData() {
				this.$api.marketMaterialPage({
					page:this.page,
					size:this.size,
					communityId:this.communityId,
					type:'COMMUNITY',//MATERIAL PRODUCT COMMUNITY
					keywords:'',
					classifyId:''
				}).then(res=>{
					console.log(res);
					if(res.status=="OK"){
                        if(this.page == 1){
                            this.shareList = []
                            if(res.list.length>=this.size){
                                this.ismore = true
                            }
                        }
                        res.list.map(item=>{
                            this.shareList.push(item)
                        })
                        console.log(this.shareList)
					}
                    uni.stopPullDownRefresh();
                    uni.hideNavigationBarLoading()
				}).catch(err=>{
					console.log(err);
				})
			},
			goDetail({id,type}){
				if(type=='VIDEO'){
					uni.navigateTo({
						url: `/pages/housekeeper-sharing-video/housekeeper-sharing-video?id=${id}`,
					});
				}else{
					uni.navigateTo({
						url: `/pages/housekeeper-sharing-img/housekeeper-sharing-img?id=${id}`,
					});
				}
			}
		}
	}
